@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.Albums-album:hover {
  color: white;
}

/* https://keithjgrant.com/posts/2017/07/transitioning-gradients/ */
.Albums-album::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: -1;
  -webkit-transition: opacity var(--common-transition-time) linear;
  transition: opacity var(--common-transition-time) linear;
  background: -webkit-gradient(
    linear,
    right bottom, left top,
    color-stop(10%, darkslateblue),
    color-stop(50%, rgba(229, 46, 113, 0.8)),
    color-stop(90%, darkslateblue)
  );
  background: linear-gradient(
    to top left,
    darkslateblue 10%,
    rgba(229, 46, 113, 0.8) 50%,
    darkslateblue 90%
  );
}

.Albums-album:hover::before {
  opacity: 1;
}

.Albums-album:hover .Albums-album-id {
  opacity: 1;
}

.Albums-album-id {
  top: 1rem;
  right: 1rem;
  opacity: 0.25;
  position: absolute;
}

.Albums-album-title {
  color: white;
}

.Photo {
  position: relative;
  justify-content: center;
}

.Photo:hover .Photo-image {
  background: rgba(255, 255, 255, 0.5) !important;
}

.Photo-image {
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-size: 500%;
  -webkit-transition: opacity var(--common-transition-time);
  transition: opacity var(--common-transition-time);
}

.Photo-image-is-loaded {
  opacity: 1;
}

.Photo-title {
  opacity: 0;
  color: white;
  word-break: break-all;
  -webkit-transition: opacity var(--common-transition-time);
  transition: opacity var(--common-transition-time);
}

.Photo:hover .Photo-title {
  opacity: 1;
}

.Photo-dismiss {
  top: 1rem;
  right: 1rem;
  width: 1rem;
  height: 1rem;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  -webkit-transition: color var(--common-transition-time);
  transition: color var(--common-transition-time);
  -webkit-transform: translate(25%, -25%);
          transform: translate(25%, -25%);
}

.Photo-dismiss::before {
  content: 'x';
  -webkit-transition: color var(--common-transition-time);
  transition: color var(--common-transition-time);
}

.Photo-dismiss:hover::before {
  color: white;
}

.Photos {
  display: flex;
  flex-direction: column;
}

.Photos-placeholder {
  position: relative;
  flex-grow: 1;
  display: flex;
  font-size: 2rem;
  min-height: 50vh;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.25);
  border: 0.5rem solid rgba(255, 255, 255, 0.25);
}

.Photos-placeholder-text {
  letter-spacing: 0.1rem;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(white),
    to(rgba(255, 255, 255, 0.25))
  );
  background-image: linear-gradient(
    180deg,
    white 0%,
    rgba(255, 255, 255, 0.25) 100%
  );
}

.Modal-photo {
  margin: auto;
}

.Modal-photo img {
  width: 100%;
  max-width: 600px;
}

.Modal-dismiss {
  top: 1rem;
  right: 1rem;
  width: 1rem;
  height: 1rem;
  display: flex;
  position: absolute;
  font-weight: bold;
  align-items: center;
  justify-content: center;
  -webkit-transform: translate(25%, -25%);
          transform: translate(25%, -25%);
}

.Modal-dismiss::before {
  content: 'x';
  -webkit-transition: color var(--common-transition-time);
  transition: color var(--common-transition-time);
}

.Modal-dismiss:hover::before {
  color: white;
}

.Gallery {
  padding-top: 4rem;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 4rem;
}

.Gallery-grid {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: 1fr 1fr;
}

@media (max-width: 768px) {
  .Gallery-grid {
    grid-template-columns: 1fr;
  }
}

:root {
  --scrollbar-thumb-width: 9px;
  --scrollbar-thumb-margin: 3px;
  --common-transition-time: 0.5s;
  --color-text: #c2c2c2;
  --color-shadow: #070509;
  --color-background: #100e17;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2 {
  margin-top: 0;
  font-size: 1rem;
  margin-bottom: 2rem;
  letter-spacing: 0.25rem;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.25);
}

a:hover {
  color: #070509;
  color: var(--color-shadow);
  background: -webkit-gradient(linear, right bottom, left top, from(darkslateblue), to(#e52e71));
  background: linear-gradient(to top left, darkslateblue, #e52e71);
}

/* Chrome-like (mobile) scrollbar */
*::-webkit-scrollbar {
  width: 9px;
  width: var(--scrollbar-thumb-width);
}

*::-webkit-scrollbar-thumb {
  border: solid 3px transparent;
  border: solid var(--scrollbar-thumb-margin) transparent;
  box-shadow: inset 0 0 9px
    9px rgba(255, 255, 255, 0.25);
  box-shadow: inset 0 0 var(--scrollbar-thumb-width)
    var(--scrollbar-thumb-width) rgba(255, 255, 255, 0.25);
}

.clearfix::before,
.clearfix::after {
  content: '';
  display: table;
  clear: both;
}

.App {
  height: 100vh;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  color: #c2c2c2;
  color: var(--color-text);
  background-color: #100e17;
  background-color: var(--color-background);
}

.grid {
  --grid-item-dim: 8rem;
  /* flex-grow: 1; */
  display: grid;
  grid-gap: 0.5rem;
  grid-template-columns: repeat(auto-fill, minmax(var(--grid-item-dim), 1fr));
}

.grid-item {
  z-index: 2;
  padding: 1rem;
  position: relative;
  font-size: 0.825rem;
  height: var(--grid-item-dim);
  display: flex;
  font-weight: bold;
  align-items: center;
  flex-direction: column;
  text-transform: uppercase;
  justify-content: space-evenly;
  background-color: #100e17;
  background-color: var(--color-background);
  box-shadow: 0 4px 80px #070509;
  box-shadow: 0 4px 80px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.035);
  -webkit-transition: box-shadow 0.5s,
    background 0.5s,
    -webkit-transform 0.5s;
  transition: box-shadow 0.5s,
    background 0.5s,
    -webkit-transform 0.5s;
  transition: transform 0.5s,
    box-shadow 0.5s,
    background 0.5s;
  transition: transform 0.5s,
    box-shadow 0.5s,
    background 0.5s,
    -webkit-transform 0.5s;
  -webkit-transition: box-shadow var(--common-transition-time),
    background var(--common-transition-time),
    -webkit-transform var(--common-transition-time);
  transition: box-shadow var(--common-transition-time),
    background var(--common-transition-time),
    -webkit-transform var(--common-transition-time);
  transition: transform var(--common-transition-time),
    box-shadow var(--common-transition-time),
    background var(--common-transition-time);
  transition: transform var(--common-transition-time),
    box-shadow var(--common-transition-time),
    background var(--common-transition-time),
    -webkit-transform var(--common-transition-time);
}

.grid-item:hover {
  z-index: 1;
  cursor: pointer;
  -webkit-transform: translateY(-0.2rem);
          transform: translateY(-0.2rem);
}

.line-clamp {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.Body {
  overflow-y: auto;
}

.Footer {
  z-index: 2;
  bottom: 0;
  width: 100%;
  margin-top: auto;
  padding: 1rem 2rem;
  font-size: 0.7rem;
  display: flex;
  font-weight: 700;
  align-items: center;
  letter-spacing: 0.25rem;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.25);
  background-color: #070509;
  background-color: var(--color-shadow);
  box-shadow: 0 4px 80px #070509;
  box-shadow: 0 4px 80px var(--color-shadow);
  border-top: 2px solid rgba(255, 255, 255, 0.05);
}

.Footer a {
  color: inherit;
  text-decoration: none;
}

.Footer a:hover {
  color: #070509;
  color: var(--color-shadow);
}

.Footer-github {
  opacity: 0.5;
  margin-left: auto;
}

.Footer-github img {
  width: 3.25rem;
}

@media (max-width: 576px) {
  .Footer-credits {
    display: none;
  }

  .Footer-github {
    margin-right: auto;
  }
}

.ReactModal__Overlay {
  opacity: 0;
  -webkit-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

